
.contact-start{
    background-image: url('../Image/mainServicePageBg.jpg');
    height: 500px;
    width: 100%;
    transition: all 0.7s ease-in 0s;
    box-shadow: rgb(0 0 20 / 40%) 0px 0px 0px 2000px inset;
    border-radius: 0px 0px 150px 150px;
}
.max-width{
    max-width: 1300px;
    padding: 0 80px;
    margin: auto;
}
.about, .services, .skills, .teams, .contact, footer{
    font-family: 'Poppins', sans-serif;
}
.about .about-content,
.services .serv-content,
.skills .skills-content,
.contact .contact-content{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}
section .title{
    position: relative;
    text-align: center;
    font-size: 40px;
    font-weight: 500;
    margin-bottom: 60px;
    padding-bottom: 20px;
    font-family: 'Ubuntu', sans-serif;
}
section .title::before{
    content: "";
    position: absolute;
    bottom: 0px;
    left: 50%;
    width: 180px;
    height: 3px;
    background: #111;
    transform: translateX(-50%);
}
section .title::after{
    position: absolute;
    bottom: -8px;
    left: 50%;
    font-size: 20px;
    color: rgb(50, 72, 143);
    padding: 0 5px;
    background: #fff;
    transform: translateX(-50%);
}
/* contact section styling */
.contact .title::after{
    content: "get in touch";
}
.contact .contact-content .column{
    width: calc(50% - 30px);
}
.contact .contact-content .text{
    font-size: 30px;
    font-weight: 600;
    margin-bottom: 10px;
}
.contact .contact-content .left p{
    text-align: justify;
}
.contact .contact-content .left .icons{
    margin: 10px 0;
}
.contact .contact-content .row{
    display: flex;
    height: 65px;
    align-items: center;
}
.contact .contact-content .row .info{
    margin-left: 30px;
    margin-top: 12px;
}
.contact .contact-content .row .info1{
    margin-left: 47px;
    margin-top: 12px;
}
.contact .contact-content .row .icon1{
    font-size: 20px;
    color: rgb(50, 72, 143);
   
}
.contact .contact-content .row .icon2{
    font-size: 25px;
    color: rgb(50, 72, 143);
 
}
.contact .contact-content .row .icon3{
    font-size: 25px;
    color: rgb(50, 72, 143);
    margin-bottom: -50px;
}
.contact .contact-content .info .head{
    font-weight: 700;
    
}
.contact .contact-content .info1 .head{
    font-weight: 700;
    
}
.contact .contact-content .info .sub-title{
    color: #333;
}
.contact .right form .fields{
    display: flex;
}
.contact .right form .field,
.contact .right form .fields .field{
    height: 45px;
    width: 100%;
    margin-bottom: 15px;
    margin-right: 50px;
}
.contact .right form .textarea{
    height: 80px;
    width: 100%;
}
.contact .right form .name{
    margin-right: 10px;
}
.contact .right form .field input,
.contact .right form .textarea textarea{
    height: 100%;
    width: 100%;
    border: 1px solid lightgrey;
    border-radius: 6px;
    outline: none;
    padding: 0 15px;
    font-size: 17px;
    font-family: 'Poppins', sans-serif;
    transition: all 0.3s ease;
}
.contact .right form .field input:focus,
.contact .right form .textarea textarea:focus{
    border-color: #b3b3b3;
}
.contact .right form .textarea textarea{
  padding-top: 10px;
  resize: none;
}
.contact .right form .button-area{
  display: flex;
  align-items: center;
}
.right form .button-area button{
  color: #fff;
  display: block;
  width: 160px!important;
  height: 45px;
  outline: none;
  font-size: 18px;
  font-weight: 500;
  border-radius: 6px;
  cursor: pointer;
  flex-wrap: nowrap;
  background: rgb(50, 72, 143);
  border: 2px solid rgb(50, 72, 143);
  transition: all 0.3s ease;
  margin-top: 20px;
}
.right form .button-area button:hover{
  color: rgb(50, 72, 143);
  background: none;
}